<template>
  <view class="box">
    <view class="cascade-container">
      <cascadeSelect :options="areaOptions" :field-names="fieldNames" ref="cascadeSelectRef" />
    </view>
    <view class="bottom-container">
      <button class="button reset" @click="reset">重置</button>
      <button class="button confirm" @click="confirm">确定</button>
    </view>
  </view>
</template>

<script>
import cascadeSelect from './cascadeSelect.vue'
export default {
  name: 'dropDownCitySelect',
  components: {
    cascadeSelect,
  },
  props: {},
  data() {
    return {
      fieldNames: { label: 'name', value: 'company_area_id', children: 'next' },
      areaOptions: [],
    }
  },
  computed: {},
  watch: {},
  created() {
    this.getAreaOption()
  },

  methods: {
    confirm() {
      let data = this.$refs.cascadeSelectRef.getValues()
      this.$emit('confirm', data)
    },
    reset() {
      this.$refs.cascadeSelectRef.reset()
      let data = this.$refs.cascadeSelectRef.getValues()
      this.$emit('reset', data)
    },

    async getAreaOption() {
      let setting = await this.$api.getAreaSetting({})
      let params = {
        high_level: setting.data.show_high_level,
        level: setting.data.show_low_level,
        with_up: 0,
      }
      const res = await this.$api.getCompanyAreaByAccount(params)
      this.areaOptions = res.data
    },
  },
}
</script>

